#show-small {
  z-index: 1;
}

#small-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: brown;
  width: 100%;
}

@media (max-width: 768px) {
  #contai {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    padding-top: 0.6rem;
  }

  #inv-logo {
    margin-top: 1.2rem;
    margin-bottom: 0.5rem;
    text-align: center;
  }

  #inv-nav {
    margin-left: 1rem;
    margin-right: 1rem;
  }
}

@media screen and (width <= 575px) {
  .first-view-main {
    margin-top: 42rem;
  }
}